.tp-brand-item {
  text-align: center;
}

.slick-slide img {
  display: inline-block;

  @media #{$xs} {
    padding: 0;
  }
}

.silder-overlay {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--tp-common-black);
    opacity: 0.3;
    z-index: -1;
  }
}
